/**
 * Get the i18n translations to all the survey languages
 * to display the language equal the language of the survey
 * @param {the array of likert values} likertValues
 */
function getLikertLabels (likertValues) {
  const newValues = JSON.parse(JSON.stringify(likertValues))
  newValues.map(likert => {
    likert.label = {
      'en_US': enUS.survey.likert[likert.label],
      'pt_BR': ptBR.survey.likert[likert.label],
      'es_ES': esES.survey.likert[likert.label]
    }
  })
  return newValues
}

/**
 * Get the i18n translations to all the survey languages
 * to display the language equal the language of the survey
 * @param {the array of textual values} textualValues
 */
function getTextualLabels (textualValues) {
  const newValues = JSON.parse(JSON.stringify(textualValues))
  newValues.map(likert => {
    likert.label = {
      'en_US': enUS.survey.textual[likert.label],
      'pt_BR': ptBR.survey.textual[likert.label],
      'es_ES': esES.survey.textual[likert.label]
    }
  })
  return newValues
}

