/**
 * Get the scale array for the question, number , likert or inverted
 * @param {min_scale} minScale
 * @param {max_scale} maxScale
 * @param {inverted_scale} reverse
 * @param {scale_type} scaleType
 * @param {the type of the question item} questionType
 */
export function getScale (minScale, maxScale, reverse, scaleType, questionType) {
  let scaleArray = []
  if (scaleType === 0) {
    for (let i = minScale; i <= maxScale; i++) {
      scaleArray.push(i)
    }
    if (reverse) {
      scaleArray = revert(scaleArray)
    }
  } else if (scaleType === 2) {
    scaleArray = getTextualScale(maxScale)
  } else {
    scaleArray = getLikertScale(questionType, maxScale)
  }

  return scaleArray
}
/**
 * Get scale array based on likert questions
 * @param {the question type(CES of CSAT)} questionType
 * @param {max_scale} maxScale
 */
export function getLikertScale (questionType, maxScale) {
  let likertScale = []
  if (questionType === consts.QUESTION_TYPE.CES || questionType === consts.QUESTION_TYPE.CES2) {
    if (maxScale === 7) {
      likertScale = [...likertValues.likert_7]
    } else {
      likertScale = [...likertValues.likert_5]
    }
  } else {
    likertScale = [...likertValues.likert_csat]
  }

  return getLikertLabels(likertScale)
}

/**
 * Get scale array based on textual questions
 * @param {The max scale to know what labels to use} maxScale
 */
export function getTextualScale (maxScale) {
  let textualScale = [...textualValues[maxScale]]

  return getTextualLabels(textualScale)
}

/**
 * Return if the question button is filled with background or bordered
 * @param {button_design} buttonDesign
 */
export function isFilled (buttonDesign) {
  return !!(buttonDesign === 1 || buttonDesign === 3)
}
/**
 * Return the radius of the question button according scale type and button design
 * @param {button_design} buttonDesign
 * @param {scale_type} scaleType
 */
export function roundedValue (buttonDesign, scaleType) {
  return buttonDesign === 1 || buttonDesign === 2 || scaleType === 1 ? '1.5px' : '100%'
}
/**
 * Get button color based on index and color pattern
 * @param {the index of the button in the array} index
 * @param {color_pattern} colorPatern
 * @param {the colors array based on scale} colors
 * @param {custom_color chosed by user} customColor
 */
export function getColor (index, colorPatern, colors, customColor) {
  if (colorPatern === 1) {
    return colors[index]// Track standard
  }
  if (colorPatern === 2) {
    return '#aaabb3'// Gray scale
  }
  return customColor // Custom
}

/**
 * Get track standard array of colors based on scale
 * @param {min_scale} minScale
 * @param {max_scale} maxScale
 * @param {inverted_scale} reverse
 */
export function getColorsArray (minScale, maxScale, reverse) {
  const min = minScale
  const max = maxScale
  let colorArray = []
  if (min === 0 && max === 10) {
    colorArray = colors.colors010
  } else if (min === 1 && max === 10) {
    colorArray = colors.colors110
  } else if (min === 1 && max === 7) {
    colorArray = colors.colors17
  } else if (min === 0 && max === 5) {
    colorArray = colors.colors05
  } else {
    colorArray = colors.colors15
  }
  if (reverse) {
    colorArray = revert(colorArray)
  }
  return colorArray
}

function revert (array) {
  return array.slice().reverse()
}

export function getCes2Question (scaleType) {
  if (scaleType === 1) {
    return {
      'en_US': enUS.survey.new.ces2_question_likert,
      'pt_BR': ptBR.survey.new.ces2_question_likert,
      'es_ES': esES.survey.new.ces2_question_likert
    }
  }
  return {
    'en_US': enUS.survey.new.ces2_question,
    'pt_BR': ptBR.survey.new.ces2_question,
    'es_ES': esES.survey.new.ces2_question
  }
}

