export default {
  namespaced: true,
  state: {
    show: false,
    token: null,
    customer: null,
    interaction: null,
    editMode: false,
  },
  mutations: {
    showWidget(state, show) {
      state.show = show
    },
    setCustomer(state, customer) {
      state.customer = customer
    },
    setInteraction(state, interaction) {
      state.interaction = interaction
    },
    setToken(state, token) {
      state.token = token
    },
    setEditMode(state, edit) {
      state.editMode = edit
    },
  },
}
