<template>
  <div>
    <div v-if="scale_type === 0">
      <div ref="buttons" class="button-row" :style="{'margin-bottom': labeled? '40px' : '0'}">
        <div v-for="(n, index) in scales" :key="index">
          <div

            @click="selectItem(n, index)"
            :ref="'ref_'+index"
            :class="['button_design', big ? 'big' : '', selected === n ? 'selected': '']"
            :style="getButtonStyle(index)"
            @mouseover.stop="onHover(true,index, n, $event)"
            @mouseout.stop="onHover(false,index, n, $event)">
            {{n}}

            <div v-if="labeled && index === 0" class="number-label desktop-only">
              <div v-for="(label,index) in labelMinArray" :key="index">
                {{label}}
              </div>
            </div>
            <div v-else-if="labeled && index === scales.length -1" class="number-label desktop-only">
              <div v-for="(label,index) in labelMaxArray" :key="index">
                {{label}}
              </div>
            </div>
          </div>

        </div>

      </div>
      <div class="flex justify-center" v-if="labeled" style="margin-top: -30px">
        <div class="legend-mobile mobile-only q-mr-md">
          <div class="number" :style="{backgroundColor: getColor(0)}">{{min_scale}} </div>
          <div class="label" :style="{color: getColor(0)}">
            {{label_min}}
          </div>
        </div>
        <div class="legend-mobile mobile-only">
          <div class="number" :style="{backgroundColor: getColor(scales.length-1)}"> {{max_scale}}</div>
          <div class="label" :style="{color: getColor(scales.length-1)}">
            {{label_max}}
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="button-row">
        <div
          :ref="'ref_'+index"
          @click="selectItem(likert.value, index)"
          v-for="(likert, index) in scales"
          :key="index"
          class="likert"
          :class="{'selected': selected === likert.value, 'scale7': max_scale === 7}"
          :style="getButtonStyle(index)"
          @mouseover="onHover(true,index,likert.value, $event)"
          @mouseout="onHover(false,index,likert.value, $event)">
          {{likert.label[language]}}
        </div>
      </div>

    </div>
  </div>
</template>
<script>
import SurveyUseCase from '../../arch/survey/usecase'
const surveyUseCase = new SurveyUseCase()

export default {
  name: 'ButtonQuestion',
  props: {
    questionType: Number,
    scale_type: Number,
    min_scale: Number,
    max_scale: Number,
    button_design: Number,
    color_pattern: Number,
    big: Boolean,
    labeled: Boolean,
    reverse: Boolean,
    label_min: String,
    label_max: String,
    custom_color: String,
    current_response: [String, Number],
    onClick: {
      type: Function,
      default: () => { }
    },
    language: {
      type: String,
      default: 'pt_BR'
    }
  },
  data () {
    return {
      selected: null,
      filled: false,
      rounded: '1.5px'
    }
  },
  computed: {
    scales () {
      return surveyUseCase.getQuestionScale(this.min_scale, this.max_scale, this.reverse, this.scale_type, this.questionType)
    },
    labelMinArray () {
      return this.label_min.split(' ')
    },
    labelMaxArray () {
      return this.label_max.split(' ')
    },
    colorsArray () {
      return surveyUseCase.getQuestionColors(this.min_scale, this.max_scale, this.reverse)
    }
  },
  watch: {
    button_design () {
      this.getDesignProperties()
    },
    scale_type () {
      this.getDesignProperties()
    },
    min_scale () {
      this.getDesignProperties()
    },
    max_scale () {
      this.getDesignProperties()
    }
  },
  methods: {
    selectItem (item, index, partial) {
      try {
        item = Number(item)
      } catch (error) {
        // Not a number
      }
      this.selected = item
      const ref = 'ref_' + index
      this.clearSelectedItems()
      if (!partial) {
        this.onClick(item)
      }
      this.changeStyleOnSelect(this.$refs[ref][0], index)
    },
    clearSelectedItems () {
      for (let i = 0; i < this.scales.length; i++) {
        if (this.$refs['ref_' + i] === undefined) {
          return
        }
        if (this.$refs['ref_' + i][0] !== undefined) {
          // Return to the old border color
          // this.$refs['ref_' + i][0].style = this.getButtonStyle(i)
          this.$refs['ref_' + i][0].setAttribute('style', this.getButtonStyle(i))
        }
      }
    },

    getButtonStyle (index) {
      let color = this.getColor(index)
      let style = ''
      if (this.filled) {
        style += `background: ${color};`
        style += `color: #FFFFFF;` // Se for todo colorido fonte branca
      } else {
        style += `color: ${color};` // Se tiver s a borda fonte colorida
      }
      style += `border-color: ${color};`
      style += `border-radius: ${this.rounded};`
      style += 'display: flex; justify-content: center; align-items: center;'

      return style
    },
    getDesignProperties () {
      this.filled = surveyUseCase.isButtonFilled(this.button_design)
      this.rounded = surveyUseCase.getButtonRoundedValue(this.button_design, this.scale_type)

      this.clearSelectedItems()
    },
    onHover (hover, index, item, e) {
      if (e.target.parentNode.className.includes('number-label')) {
        return
      }
      const button = e.target

      if (this.selected === item) {
        return
      }
      this.changeDesignOnHover(button, hover, index)
    },
    changeDesignOnHover (button, hover, index) {
      if ((this.button_design === 2 || this.button_design === 4)) { // Design with only border, invert hover
        hover = !hover
      }
      const color = this.getColor(index)

      if (hover) {
        button.setAttribute('style', this.getButtonStyle(index) + `;background: transparent;color: ${color}`)
        // button.style.background = 'transparent'
        // button.style.color = color
      } else {
        button.setAttribute('style', this.getButtonStyle(index) + `;background: ${color};color: #FFFFFF`)
        // button.style.background = color
        // button.style.color = '#FFFFFF'
      }
    },
    changeStyleOnSelect (button, index) {
      this.changeDesignOnHover(button, true, index)
    },
    getColor (index) {
      return surveyUseCase.getButtonColor(index, this.color_pattern, this.colorsArray, this.custom_color)
    },
    /*
     * If the responses of this survey was saved partial, select the partial answer
     */
    selectPartialValue () {
      if (this.current_response != null) {
        let index = -1
        if (this.scale_type === 0) {
          index = this.scales.findIndex(scale => scale === Number(this.current_response))
        } else {
          index = this.scales.findIndex(scale => scale.value === Number(this.current_response))
        }
        if (index !== undefined && index !== -1) {
          this.selectItem(Number(this.current_response), index, true)
        }
      }
    }
  },
  created () {
    this.getDesignProperties()
  },
  mounted () {
    this.selectPartialValue()
  }
}
</script>
