import { getColorsArray, getCes2MinButtonScale, getCes2MaxButtonScale, getScale, getColor, isFilled, roundedValue, getCes2Question, multipleChoiceLetters, optionOtherLabel, commentPlaceholder } from './utils'

export default class SurveyUseCase {
  getQuestionColors (minScale, maxScale, reverse) {
    return getColorsArray(minScale, maxScale, reverse)
  }
  getQuestionScale (minScale, maxScale, reverse, scaleType, likertType) {
    return getScale(minScale, maxScale, reverse, scaleType, likertType)
  }
  getButtonColor (index, colorPatern, colors, customColor) {
    return getColor(index, colorPatern, colors, customColor)
  }
  getButtonRoundedValue (buttonDesign, scaleType) {
    return roundedValue(buttonDesign, scaleType)
  }
  isButtonFilled (buttonDesign) {
    return isFilled(buttonDesign)
  }
  getMultipleChoiceLetters () {
    return multipleChoiceLetters
  }
  getOptionOtherLabel (language) {
    return optionOtherLabel[language]
  }
  getCommentPlaceholder (language) {
    return commentPlaceholder[language]
  }


  getCes2Question (scaleType) {
    return getCes2Question(scaleType)
  }
  getCes2MinButtonScale (maxScale) {
    return getCes2MinButtonScale(maxScale)
  }
  getCes2MaxButtonScale (maxScale) {
    return getCes2MaxButtonScale(maxScale)
  }


}
