<template>
  <div v-if="!showThanksMessage">
    <div class="track-question">
      <div class="error" v-if="showErrorMessage">
        {{ userLanguage.survey.messages.obligatory }}
      </div>
      <DisplayWeb
        :selectedQuestion="currentItem"
        :color="widget.survey.primary_color"
        :lang="language"
        :onSelectAnswer="item => selectValue(item)"
        :status="widget.survey.status"
        :currentAnswer="currentAnswer"
      />
    </div>
    <div class="track-footer">
      <a href="https://track.co" target="_blank"><img :src="logo" width="30"/></a>
      <div class="track-buttons">
        <Button :label="userLanguage.survey.buttons.previous" v-if="showPrevious" :handleClick="handlePreviousItem" />
        <Button :label="userLanguage.survey.buttons.next" height="30px" v-if="showNext" :handleClick="handleNextItem" />
        <Button :label="userLanguage.survey.buttons.send" v-if="showSubmit" :handleClick="submitCompleteAnswer" />
      </div>
    </div>
  </div>
  <div v-else class="track-message" :style="{ color: colors.primaryColor }">
    {{ widget.thanks_message[language] || defaultMessage[language] }}
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
import { Button } from '@tracksale/design-system'
import consts from '../../consts'
import { userLanguage } from '../../utils/language'
import { getUserContextByMetric } from '../../utils/survey'
import DisplayWeb from '../../components/survey/DisplayWeb'

import WidgetUseCase from '../../arch/widget/usecase'
const widgetUseCase = new WidgetUseCase()

export default {
  components: {
    DisplayWeb,
    Button,
  },
  props: {
    widget: Object,
  },
  data() {
    return {
      answers: [],
      startTime: 0,
      currentIndex: 0,
      interactionUUID: null,
      showThanksMessage: false,
      showErrorMessage: false,
      language: 'pt_BR',
      defaultMessage: {
        en_US: 'Thank you for your feedback!',
        pt_BR: 'Agradecemos por seu feedback!',
        es_ES: 'Gracias por tus comentarios!',
      },
      logo:
        'data:image/png;base64,',
    }
  },
  computed: {
    ...mapState({
      customer: state => state.survey.customer,
      interaction: state => state.survey.interaction,
      editMode: state => state.survey.editMode,
    }),
    currentItem() {
      return this.widget.survey.items[this.currentIndex]
    },
    currentAnswer() {
      return this.getCurrentAnswer(this.currentIndex)
    },
    userLanguage() {
      return userLanguage()
    },
    showNext() {
      if (this.currentIndex === this.widget.survey.items.length - 1) {
        return false
      }
      return true
    },
    showPrevious() {
      if (this.currentIndex && !this.widget.survey.jump_rules_enabled) {
        return true
      }
      return false
    },
    showSubmit() {
      if (this.currentIndex === this.widget.survey.items.length - 1) {
        return true
      }
      return false
    },
    colors() {
      let primaryColor = this.widget.survey.primary_color || '#354052'
      let secondaryColor = '#fff'
      if (!this.widget.survey_colors) {
        secondaryColor = this.widget.secondary_color
        primaryColor = this.widget.primary_color
      }
      return { primaryColor, secondaryColor }
    },
  },
  methods: {
    ...mapMutations({
      showWidget: 'survey/showWidget',
    }),
    selectValue(item) {
      const answerIndex = this.answers.findIndex(answer => answer.uuid === item.uuid)
      if (item.answer.comment) {
        item.answer.comment = item.answer.comment.trim()
      }
      if (answerIndex === -1) {
        this.answers = this.answers.concat(item)
      } else {
        const newAnswers = this.answers.filter((i, index) => index !== answerIndex)
        this.answers = newAnswers.concat(item)
      }
    },
    handlePreviousItem() {
      this.currentIndex--
    },
    handleNextItem() {
      if (!this.validateQuestion()) {
        return
      }
      const currentQuestionItem = this.widget.survey.items[this.currentIndex]
      const currentAnswer = this.answers[this.currentIndex]

      // Question cursor to next question
      this.currentIndex++

      if (currentQuestionItem.jump_rules_enabled) {
        if (!currentAnswer || currentAnswer.answer.response.length === 0) {
          this.currentIndex--
          return
        }
        const context = getUserContextByMetric(
          currentAnswer.type,
          currentAnswer.answer.response,
          currentQuestionItem.min_scale,
          currentQuestionItem.max_scale,
        )
        const action = currentQuestionItem.jump_rules[context].action
        if (action === consts.JUMP_ACTIONS.SPECIFIC_QUESTION) {
          this.currentIndex = currentQuestionItem.jump_rules[context].value
        } else if (action === consts.JUMP_ACTIONS.FINISH_SURVEY) {
          this.submitCompleteAnswer(consts.INTERACTION_STATUS.DONE)
          return
        }
      }

      if (this.answers.length) {
        this.submitPartialAnswer(consts.INTERACTION_STATUS.PARTIAL)
      }
    },
    async submitPartialAnswer() {
      if (this.editMode) {
        return
      }
      const totalTime = new Date().getTime() - this.startTime
      const surveyAnswer = {
        questions: this.answers,
        response_time: totalTime > 0 ? totalTime : 0,
        status: consts.INTERACTION_STATUS.PARTIAL,
        interaction_uuid: this.interactionUUID,
        language: this.language,
        customer: this.customer,
        interaction: this.interaction,
      }

      this.interactionUUID = await widgetUseCase.postPartialAnswer(this.widget.survey_public_hash, surveyAnswer)
    },
    async submitCompleteAnswer() {
      if (!this.validateQuestion()) {
        return
      }
      if (this.editMode) {
        this.showThanksMessage = true
        const vm = this
        setTimeout(function() {
          vm.clearWidget()
        }, 2000)
        return
      }
      const totalTime = new Date().getTime() - this.startTime
      const surveyAnswer = {
        questions: this.answers,
        response_time: totalTime > 0 ? totalTime : 0,
        status: consts.INTERACTION_STATUS.DONE,
        interaction_uuid: this.interactionUUID,
        language: this.language,
        customer: this.customer,
        interaction: this.interaction,
      }
      await widgetUseCase.postCompleteAnswer(this.widget.survey_public_hash, surveyAnswer)
      this.showThanksMessage = true
      const vm = this
      setTimeout(function() {
        vm.showWidget(false)
      }, 2000)
    },
    getCurrentAnswer(questionIndex) {
      const currentUuid = this.widget.survey.items[questionIndex].item_uuid
      return this.answers.find(a => a.uuid === currentUuid) || null
    },
    validateQuestion() {
      let validator = true

      const question = this.widget.survey.items[this.currentIndex]
      if (this.currentAnswer === null && (question.is_obligatory || question.jump_rules_enabled)) {
        validator = false
      } else if (this.currentAnswer !== null) {
        if (question.is_obligatory || question.jump_rules_enabled) {
          if (question.type !== consts.QUESTION_TYPE.OPEN && this.currentAnswer.answer.response.length === 0) {
            validator = false
          } else if (question.type === consts.QUESTION_TYPE.OPEN && this.currentAnswer.answer.comment === '') {
            validator = false
          }
        }
      }

      if (!validator) {
        this.showErrorMessage = true
        const vm = this
        setTimeout(function() {
          vm.showErrorMessage = false
        }, 3000)
      }
      return validator
    },
    clearWidget() {
      this.currentIndex = 0
      this.answers = []
      this.startTime = 0
      this.interactionUUID = null
      this.showThanksMessage = false
    },
  },
  created() {
    this.startTime = new Date().getTime()

    //Set user language
    this.language = widgetUseCase.setLanguage(this.widget)
  },
}
</script>
<style scoped>
.track-footer {
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.track-question {
  max-height: 500px;
  overflow: auto;
  padding: 20px;
}
.track-buttons {
  display: flex;
}
.track-buttons > button {
  margin-right: 10px;
}
.track-message {
  padding: 20px;
}
.error {
  color: #ea575f;
  margin-bottom: 10px;
}
</style>
