import http from '../../utils/http'

export default class WidgetService {
  getOne(token) {
    const config = {
      headers: {
        pathname: window.location.pathname,
      },
    }
    return http.get('/widget/' + token, config)
  }
  postOpenCounter(token) {
    return http.post('/widget/show/' + token)
  }
  postPartialAnswer(surveyPublicHas, answers) {
    return http.post('/survey/partial/' + surveyPublicHas, answers)
  }
  postCompleteAnswer(surveyPublicHas, answers) {
    return http.post('/survey/' + surveyPublicHas, answers)
  }
  checkCustomerRecurrence(token, customer) {
    return http.post('/widget/' + token + '/check', customer)
  }
}
