<template>
  <div
    :class="widget.mode === consts.WIDGET_MODE.EMBEBED ? 'widget-embebed' : 'widget-fixed'"
    class="box"
    :style="{ background: colors.secondaryColor }"
    v-if="!minimized"
  >
    <div id="track__widget-close" class="icon-close">
      <Icon
        name="Close"
        :color="colors.primaryColor"
        class="cursor-pointer"
        :handleClick="() => minimizeWidget(true)"
      />
    </div>
    <Survey :widget="widget" />
  </div>
  <div v-else class="widget-minimized">
    <Icon name="Open" :color="colors.primaryColor" class="cursor-pointer" :handleClick="() => minimizeWidget(false)" />
  </div>
</template>
<script>
import consts from '../../consts'
import Icon from '../../components/Icon'
import Survey from './Survey'

export default {
  components: { Icon, Survey },
  props: {
    widget: Object,
  },
  data() {
    return {
      consts: consts,
      minimized: false,
    }
  },
  computed: {
    colors() {
      let primaryColor = this.widget.survey.primary_color || '#354052'
      let secondaryColor = '#fff'
      if (!this.widget.survey_colors) {
        secondaryColor = this.widget.secondary_color
        primaryColor = this.widget.primary_color
      }
      return { primaryColor, secondaryColor }
    },
  },
  methods: {
    minimizeWidget(minimize) {
      console.log(minimize)
      this.minimized = minimize
    },
  },
  created() {
    this.minimized = this.widget.minimized
  },
}
</script>
<style scoped>
.box {
  background-color: white;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border: 1px solid transparent;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
}
.widget-fixed {
  position: fixed;
  bottom: 0px;
  right: 35px;
  max-height: 600px;
  max-width: 450px;
  width: 360px;
  z-index: 99999;
}
.widget-embebed {
  position: relative;
  width: 100%;
  max-height: 600px;
}
.icon-close {
  padding: 10px;
  text-align: right;
}
.cursor-pointer {
  cursor: pointer;
}

.widget-minimized {
  all: unset;
  position: fixed;
  right: 35px;
  bottom: 0;
  width: 56px;
  height: 40px;
  font-family: sans-serif;
  z-index: 2000;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border: 1px solid transparent;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
