import store from '../store'

export default function useIframeControl () {
    function getTokenKey () {
        const query = new URLSearchParams(window.location.search)
        const apiKey = query.get('key') ?? ''
        const edit = query.get('edit') ?? ''
        store.commit('survey/setToken', apiKey)
        store.commit('survey/setEditMode', edit)
    }

    function updateDataListener () {
        window.addEventListener('message', (event) => {
            if (!event.data.isWidget) return

            if (event.data.customer) {
                console.log(event.data.customer)
                store.commit('survey/setCustomer', event.data.customer)
            }
            if (event.data.interaction) {
                store.commit('survey/setInteraction', event.data.interaction)
            }
        })
    }

    return {
        updateDataListener,
        getTokenKey
    }
}