import consts from '../consts'

function getNPS (userResponse) {
    if (userResponse > 8) {
        return consts.JUMP_LOGIC_PATHS.PROMOTER
    } else if (userResponse > 6) {
        return consts.JUMP_LOGIC_PATHS.PASSIVE
    } else {
        return consts.JUMP_LOGIC_PATHS.DETRACTOR
    }
}

function getZeroToTen (userResponse) {
    if (userResponse > 8) {
        return consts.JUMP_LOGIC_PATHS.PROMOTER
    } else if (userResponse > 6) {
        return consts.JUMP_LOGIC_PATHS.PASSIVE
    } else {
        return consts.JUMP_LOGIC_PATHS.DETRACTOR
    }
}

function getOneToTen (userResponse) {
    if (userResponse > 6) {
        return consts.JUMP_LOGIC_PATHS.PROMOTER
    } else if (userResponse > 4) {
        return consts.JUMP_LOGIC_PATHS.PASSIVE
    } else {
        return consts.JUMP_LOGIC_PATHS.DETRACTOR
    }
}

function getOneToSeven (userResponse) {
    if (userResponse > 4) {
        return consts.JUMP_LOGIC_PATHS.PROMOTER
    } else if (userResponse > 3) {
        return consts.JUMP_LOGIC_PATHS.PASSIVE
    } else {
        return consts.JUMP_LOGIC_PATHS.DETRACTOR
    }
}

function getZeroToFive (userResponse) {
    if (userResponse > 3) {
        return consts.JUMP_LOGIC_PATHS.PROMOTER
    } else if (userResponse > 1) {
        return consts.JUMP_LOGIC_PATHS.PASSIVE
    } else {
        return consts.JUMP_LOGIC_PATHS.DETRACTOR
    }
}

function getOneToFive (userResponse) {
    if (userResponse > 3) {
        return consts.JUMP_LOGIC_PATHS.PROMOTER
    } else if (userResponse > 2) {
        return consts.JUMP_LOGIC_PATHS.PASSIVE
    } else {
        return consts.JUMP_LOGIC_PATHS.DETRACTOR
    }
}

export function getUserContextByMetric (metricType, userResponse, minScale, maxScale) {
    switch (metricType) {
        case consts.QUESTION_TYPE.CES:
        case consts.QUESTION_TYPE.CES2:
        case consts.QUESTION_TYPE.CSAT:
        case consts.QUESTION_TYPE.EMOJI:
        case consts.QUESTION_TYPE.RATINGS:
            if (minScale === 0 && maxScale === 10) {
                return getZeroToTen(userResponse)
            } else if (minScale === 1 && maxScale === 10) {
                return getOneToTen(userResponse)
            } else if (minScale === 1 && maxScale === 7) {
                return getOneToSeven(userResponse)
            } else if (minScale === 0 && maxScale === 5) {
                return getZeroToFive(userResponse)
            } else {
                return getOneToFive(userResponse)
            }
        case consts.QUESTION_TYPE.NPS_RELATIONAL:
        case consts.QUESTION_TYPE.NPS_TRANSACTIONAL:
            return getNPS(userResponse)
        case consts.QUESTION_TYPE.THUMBS:
            if (userResponse > 0) {
                return consts.JUMP_LOGIC_PATHS.PROMOTER
            } else {
                return consts.JUMP_LOGIC_PATHS.DETRACTOR
            }
        default:
            return consts.JUMP_LOGIC_PATHS.PASSIVE
    }
}
