<template>
  <QuestionContainer
    :status="status"
    :questionText="selectedQuestion.question[lang]"
    :isObligatory="selectedQuestion.is_obligatory"
    :color="color"
  >
    <template v-slot:buttons>
      <div>
        <button-question
          :questionType="selectedQuestion.type"
          :scale_type="0"
          :min_scale="0"
          :max_scale="10"
          :button_design="selectedQuestion.button_design"
          :color_pattern="selectedQuestion.color_pattern"
          :reverse="selectedQuestion.inverted_scale"
          :custom_color="selectedQuestion.custom_color"
          :onClick="item => selectAnswer(item)"
          :current_response="currentAnswer ? currentAnswer.answer.response : null"
          :language="lang"
        />
      </div>
    </template>
    <template v-slot:comment>
      <div v-if="selectedQuestion.comments_enabled">
        <!-- Detractor question -->
        <div v-show="checkDetractorAnswer() === 'detractor'" class="subtitle">
          <div
            v-if="
              selectedQuestion.detractor_comment_question !== undefined &&
                selectedQuestion.detractor_comment_question[lang] !== ''
            "
          >
            {{ selectedQuestion.detractor_comment_question[lang] }}
          </div>
          <input
            v-model="comment"
            autogrow
            :placeholder="placeholder"
            class="survey-input"
            :rules="[rules.max]"
            @blur="selectItem(answer)"
          />
        </div>

        <!-- Neutral question -->
        <div v-show="checkDetractorAnswer() === 'neutral'" class="subtitle">
          <div
            v-if="
              selectedQuestion.neutral_comment_question !== undefined &&
                selectedQuestion.neutral_comment_question[lang] !== ''
            "
          >
            {{ selectedQuestion.neutral_comment_question[lang] }}
          </div>
          <input
            v-model="comment"
            autogrow
            :placeholder="placeholder"
            class="survey-input"
            :rules="[rules.max]"
            @blur="selectItem(answer)"
          />
        </div>

        <!-- Promoter question -->
        <div v-show="checkDetractorAnswer() === 'promoter'" class="subtitle">
          <div
            v-if="
              selectedQuestion.promoter_comment_question !== undefined &&
                selectedQuestion.promoter_comment_question[lang] !== ''
            "
          >
            {{ selectedQuestion.promoter_comment_question[lang] }}
          </div>
          <input
            v-model="comment"
            autogrow
            :placeholder="placeholder"
            class="survey-input"
            :rules="[rules.max]"
            @blur="selectItem(this.answer)"
          />
        </div>
      </div>
    </template>
  </QuestionContainer>
</template>
<script>
import QuestionContainer from './QuestionContainer'
import ButtonQuestion from './ButtonQuestionComponent'
import SurveyUseCase from '../../arch/survey/usecase'
const surveyUseCase = new SurveyUseCase()

export default {
  components: { ButtonQuestion, QuestionContainer },
  props: {
    selectedQuestion: {
      type: Object,
    },
    lang: {
      type: String,
      default: 'pt_BR',
    },
    nps_type: {
      type: String,
    },
    onSelectAnswer: {
      type: Function,
      default: () => {},
    },
    color: {
      type: String,
    },
    status: {
      type: Number,
      default: 1,
    },
    currentAnswer: {
      type: Object,
    },
  },
  data() {
    return {
      comment: '',
      answer: '',
      rules: {
        max: v => v.length <= 5000 || this.$t('general.rules.max_length') + ' 5000',
      },
    }
  },
  computed: {
    placeholder() {
      return surveyUseCase.getCommentPlaceholder(this.lang)
    },
  },
  methods: {
    selectItem() {
      const response = {
        uuid: this.selectedQuestion.item_uuid,
        type: this.selectedQuestion.type,
        answer: {
          response: this.answer,
          comment: this.comment,
        },
      }

      this.onSelectAnswer(response)
    },
    checkDetractorAnswer() {
      if (this.answer === '') {
        return
      }
      const answerNumber = Number(this.answer)
      if (answerNumber === 10 || answerNumber === 9) {
        return 'promoter'
      } else if (answerNumber === 7 || answerNumber === 8) {
        return 'neutral'
      } else {
        return 'detractor'
      }
    },
    selectAnswer(item) {
      this.answer = item
      this.selectItem(item)
    },
  },
  mounted() {
    if (this.currentAnswer?.answer?.comment) {
      this.comment = this.currentAnswer.answer.comment
    }
    if (this.currentAnswer?.answer?.response != null) {
      this.answer = Number(this.currentAnswer.answer.response)
    }
    // this.$watch('comment', () => this.selectItem(this.answer))
  },
}
</script>
