import WidgetService from './service'
import consts from '../../consts'
import store from '../../store'
import { getUserLanguage, getSurveyLanguage } from '../../utils/language'

/**
 * Set widgetStorage in localStorage
 * show_at = timestamp that widget was view by user
 * answered_at = timestamp that user answer the widget
 * @param {widgetStorage} widgetStorage
 */
const setStorage = widgetStorage => {
  localStorage.setItem(store.state.survey.token, JSON.stringify(widgetStorage))
}
/**
 * Get widgetStorage in localStorage
 */
const getStorage = () => {
  const widgetStorage = JSON.parse(localStorage.getItem(store.state.survey.token))
  return widgetStorage
}

export default class WidgetUseCase {
  getOne(token) {
    const widgetService = new WidgetService()
    return widgetService.getOne(token)
  }
  canShow(widget) {
    const widgetStorage = getStorage()
    if (!widgetStorage) {
      // Dont have storage setted, show widget
      return true
    }

    if (widget.show_widget === consts.SHOW_WIDGET.ONCE) {
      // If have storage and is setted, don't show widget
      if (widgetStorage) {
        return false
      }
    }

    if (widget.show_widget === consts.SHOW_WIDGET.RECURRENTLY) {
      let show = false
      if (widgetStorage.answered_at) {
        // Check days of response is more than condition
        let date = new Date(widgetStorage.answered_at)
        date.setDate(date.getDate() + widget.days_after_response)
        if (new Date().getTime() >= date.getTime()) {
          show = true
        }
      }
      if (widgetStorage.show_at && !widgetStorage.answered_at) {
        // Check days of decline is more than condition
        let date = new Date(widgetStorage.show_at)
        date.setDate(date.getDate() + widget.days_after_decline)
        if (new Date().getTime() >= date.getTime()) {
          show = true
        }
      }
      return show
    }
    return true
  }
  setShowAt() {
    const widgetStorage = getStorage()
    if (!widgetStorage) {
      setStorage({ show_at: new Date().getTime() })
    }
  }
  /**
   * Set answered at in localStorage
   */
  setAnsweredAt() {
    const widgetStorage = getStorage()
    if (!widgetStorage) {
      return
    }
    widgetStorage.answered_at = new Date().getTime()
    setStorage(widgetStorage)
  }
  postOpenCounter() {
    const widgetService = new WidgetService()
    widgetService.postOpenCounter(store.state.survey.token)
  }
  /**
   * Remove non question items from survey
   * Like IMAGE and LABEL
   */
  removeNonQuestionItems(widget) {
    if (widget.survey.jump_rules_enabled) {
      return
    }
    widget.survey.items = widget.survey.items.filter(
      item => item.type !== consts.QUESTION_TYPE.LABEL && item.type !== consts.QUESTION_TYPE.IMAGE,
    )
    return widget
  }
  setLanguage(widget) {
    const userLang = getUserLanguage()
    const lang = getSurveyLanguage(userLang, widget)
    return lang
  }
  async postPartialAnswer(surveyPublicHash, answers) {
    const widgetService = new WidgetService()
    const response = await widgetService.postPartialAnswer(surveyPublicHash, answers)
    this.setAnsweredAt()
    return response
  }
  async postCompleteAnswer(surveyPublicHash, answers) {
    const widgetService = new WidgetService()
    const response = await widgetService.postCompleteAnswer(surveyPublicHash, answers)
    this.setAnsweredAt()
    return response
  }

  checkCustomerRecurrence(token, customer){
    const widgetService = new WidgetService()
    const data = {
      customer
    }
    return widgetService.checkCustomerRecurrence(token, data)
  }
}
